.f_p{position:relative;display:grid;grid-template-columns:2fr 1fr;grid-template-rows:repeat(1, minmax(50px, 1fr));border-bottom:1px solid #eee;padding:2rem 0}.f_p:last-child{border-bottom:none}@media screen and (max-width: 900px){.f_p{grid-template-columns:1fr}}.f_q{box-sizing:border-box;align-self:center;margin:0.5rem 0}@media screen and (max-width: 900px){.f_q{justify-self:flex-start}}.f_r{display:flex;flex-direction:column;align-items:flex-start;color:black}@media screen and (max-width: 400px){.f_r{padding-right:4rem}}.f_s{margin-bottom:0.5rem}.f_t{font-size:0.8rem;color:#796767;text-align:left}@media screen and (max-width: 900px){.f_u{position:absolute;top:24px;right:12px;justify-self:flex-start}}
